@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
:root{
  --blue-color: #292361;
  --dark--blue-color: #544F81
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.form{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 30px;
    height: calc(100vh - 200px);
}


input[type='text']{
    width: 100%;
    font-size: 20px;
    padding: 5px 10px;
    margin: 20px 0;
}
label[for='add']{
    font-size: 22px;
    font-weight: bold;
}
.date{
    margin: 50px 0;
}
label[for=date]{
    margin-right: 20px;
}
.form button {
    padding: 10px 30px;
    text-transform: uppercase;
    background-color: var(--blue-color);
    color: #fff;
    border: none;
    border-radius: 20px;
    font-size: 20px;
}
.task{
    flex-basis: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    margin: 2.5%;
    border: 2px solid #F1F1F1;
    border-radius: 30px;
    text-align: center;
}

.task__title{
    font-size: 18px;
    margin-bottom: 15px;
}

.task__btns button{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    padding: 5px 10px;
    margin: 10px 0;
    border: none;
}
.task__btns button .fas{
    margin-right: 4px;
}
.fa-check-circle{
    font-size: 18px;
    color: green
}
.fa-times{
    font-size: 18px;
    color: rgb(175, 0, 0)
}
.active-tasks{
    padding: 20px 0;
}
.priotity{
    margin-bottom: 20px;
    
}
.priority__title, .not-priority__title{
    display: inline-block;
    font-size: 24px;
    font-weight: bold;
    margin: 0 20px 20px 0;
}
.priority__counter{
    color: #B8B8B8;
}
.line{
    display: block;
    width: 50%;
    transition: .3s;
}
.line--red{
    border-bottom: 3px solid #FE6665;
}
.line--blue{
    border-bottom: 3px solid #6471FE
}

.priority__title:hover .line, .not-priority__title:hover .line {
    width: 100%;
}
.active-tasks__container{
    display: flex;
    flex-wrap: wrap;
}
.bottom-panel{
    position: fixed;
    display: flex;
    justify-content: space-around;
    align-items: center;
    bottom: 0;
    left: 0;
    right: 0;
    height: 70px;
    padding: 0 20px;
    background-color: var(--blue-color)
}

.bottom-panel a{
    background-color: transparent;
    border: none;
    width: 36px;
    height: 36px;
    transition: .2s;
}

.bottom-panel .tasks{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    width: 40px;
    height: 40px;
}

.bottom-panel .tasks span{
    height: 14px;
    width: 14px;
    border-radius: 4px;
    background-color: var(--dark--blue-color);
}
.bottom-panel .add-task{
    position: relative;
    background-color: var(--dark--blue-color);
    border-radius: 10px;
}
.add-task span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 3px;
    width: 45%;
    border-radius: 10px;
    background-color: var(--blue-color);
}
.add-task span:nth-of-type(2){
    transform: translate(-50%,-50%) rotate(90deg);
}
.bottom-panel .done-tasks{
    position: relative;
    background-color: var(--dark--blue-color);
    border-radius: 50%;
}

.bottom-panel .done-tasks::after{
    content: '';
    position: absolute;
    right: -5px;
    top: -5px;
    height: 20px;
    width: 20px;
    background-color: #544F81;
    border-bottom: 4px solid var(--blue-color);
    border-left: 4px solid var(--blue-color);
    border-radius: 0 150% 0 8px;
}

.bottom-panel .tasks.active{
    background-color: transparent
}
.bottom-panel .active, 
.bottom-panel .active::after, 
.bottom-panel .tasks.active span {
    background-color: #fff;
}

.App{
    padding: 20px 20px 70px;
}
header{
    /* display: flex; */
    align-items: flex-end;
    background-color: #FBFBFB;
}
header h2{
    font-size: 14px;
    flex-grow: 2;
    text-align: right;
}
.welcome {
    display: flex;
}

.welcome img{
    width: 30%;
}
